.main {
  width: 100%;
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
  z-index: 1;
  scroll-snap-type: y mandatory;
}

.main__section {
  box-sizing: border-box;
}

.main__intro {
  background-image: var(--introbg);
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;

}

@media screen and (max-width: 1400px) {
  .main__intro {

    background-position: top -30px left 0;
  }
  
}

.main__about {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
  padding-bottom: 100px;
}

.main__wakesurf {
  background-image: var(--wakesurfbg);
  background-repeat: no-repeat;
  background-size: cover;
}

.main__boatrent {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
  padding-top: 50px;
}

.main__wakeboard {
  padding-top: 50px;
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
}

.main__slider {
  background-color: var(--color-font);
}

.main__certificats {
  padding-top: 50px;
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
}

.main__faq {
  padding-top: 50px;
  background: linear-gradient(261.21deg, #e3e8e2 4.83%, #ecebe7 85.85%);
}

.main__footer {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
}


